import React, { useState, useEffect } from 'react'
import { Carousel } from 'react-bootstrap'
import { START_GAME, GET_CUSTOMER_TICKETS, CUSTOMER_TICKETS } from '../api/Endpoints'
import Axios from 'axios'
import LoaderComponent from '../components/Loader.jsx'
import CardsDialog from '../components/CardsDialog.jsx'
import _ from 'lodash'
import FindInPageIcon from '@material-ui/icons/Search'
import { Row, Col } from 'react-bootstrap'
import {
    SwipeableDrawer,
    Snackbar
} from '@material-ui/core'
import { Alert } from '@material-ui/lab'
import { makeStyles } from '@material-ui/core/styles'
import { Fireworks } from 'fireworks/lib/react'
import ReactAudioPlayer from 'react-audio-player'

const useStyles = makeStyles({
    paper: {
        background: 'black',
        color: 'white',
        width: '90%'
    }
})

const StartGame = (props) => {

    const importAll = (r, removeable) => {
        let audios = {};
        r.keys().map((item, index) => { audios[item.replace(removeable, '')] = r(item); });
        return audios;
    }
    const GameAudios = importAll(require.context('../audio/', false, /\.(mp3)$/), '../audio/')
    const NumberAudios = importAll(require.context('../audio/number/', false, /\.(mp3)$/), '../audio/number/')

    let fxProps = {
        count: 3,
        interval: 1000,
        colors: ['#cc3333', '#4CAF50', '#81C784'],
        calc: (props, i) => ({
            ...props,
            x: (i + 1) * (window.innerWidth / 3) - (i + 1) * 100,
            y: 200 + Math.random() * 100 - 50 + (i === 2 ? -80 : 0)
        })
    }
    const styles = useStyles()
    const [gameData, setGameData] = useState({})
    const [toggleDrawer, setToggleDrawer] = useState(false)
    const [isSearchingTicket, setIsSearchingTicket] = useState(false)
    const [searchableDigits, setSearchableDigits] = useState('')
    const [searchableTickets, setSearchableTickets] = useState({})
    const [isSnackbarOpen, setIsSnackbarOpen] = useState(false)
    const [calledNumbers, setCalledNumbers] = useState([])
    const [sound, setSound] = useState(0)
    const [customerTickets, setCustomerTickets] = useState({})
    const [winningTypes, setWinningTypes] = useState([])
    const [winningRecords] = useState(['quick_five', 'box_bonus', 'top_line', 'middle_line', 'bottom_line', 'star', 'first_full_house', 'second_full_house', 'third_full_house'])
    const [showCard, setShowCard] = useState(false)
    const [cardData, setCardData] = useState({})
    const [showFireworks, setShowFireworks] = useState(false)
    const [numberSound, setNumberSound] = useState(0)

    useEffect(() => {
        //run
        const fetchMyAPI = async () => {
            const result = await handleFetchGameData()
            setGameData(result.data.pd)
            let called_numbers = []
            let i = 0
            let storeTypesOfWinners = []
            result.data.pd.called_numbers.forEach(number => {
                called_numbers.push(number)
                let number_winners = result.data.pd.winners[number]
                for (const win_type in number_winners) {
                    if (number_winners.hasOwnProperty(win_type)) {
                        storeTypesOfWinners = [
                            ...storeTypesOfWinners,
                            winningRecords[i++] = {
                                type: win_type,
                                winners: number_winners[win_type],
                                numbers: [...called_numbers]
                            }
                        ]
                    }
                }
            })
            setWinningTypes([...storeTypesOfWinners])
            setCalledNumbers(called_numbers)
            await numberCall(result.data.pd, called_numbers, storeTypesOfWinners, result.data.pd.winners)
            await handleGetCustomerTickets()
        }
        fetchMyAPI()
    }, [])

    const handleFetchGameData = async () => {
        try {
            return await Axios.get(START_GAME)
        } catch (e) {
            console.log(e)
        }
    }

    function json2array(json) {
        var result = [];
        var keys = Object.keys(json);
        keys.forEach(function (key) {
            result.push(json[key]);
        });
        return result;
    }

    const updateSearchableNumber = (n) => {
        let number = Number(searchableDigits + n)
        if (number <= 600) {
            setIsSnackbarOpen(false)
            setSearchableDigits(number)
        } else {
            setSearchableDigits('')
            setIsSnackbarOpen('Invalid Ticket Number')
        }
    }

    const ticketsSearcher = async () => {
        if (searchableDigits < 1) {
            setIsSnackbarOpen('Please choose a number from 1 to 600')
        } else {
            setIsSearchingTicket(true)
            try {
                const tickets = await Axios.get(GET_CUSTOMER_TICKETS + searchableDigits)
                setSearchableTickets(tickets.data.tickets)
            } catch (e) {
                setIsSnackbarOpen('Something went wrong while searching your ticket(s), please try again')
            }
            setIsSearchingTicket(false)
        }
    }

    const numberCall = async (result, called_numbers, storeTypesOfWinners, theWinners) => {
        if (!_.isUndefined(result.next_numbers)) {
            if (!_.isUndefined(result.next_numbers.length)) {
                if (result.next_numbers.length) {
                    let cnum = result.next_numbers.shift()
                    setTimeout(() => {
                        setNumberSound(cnum)
                        setCalledNumbers([...called_numbers, cnum])
                        called_numbers.push(cnum)
                    }, 4000)
                    let next_call = 5000
                    let getWinners = theWinners[cnum]
                    let i = 0
                    let storeTypesOfWinnersOnPlay = []
                    if (!_.isUndefined(getWinners)) {
                        for (const win_type in getWinners) {
                            if (getWinners.hasOwnProperty(win_type)) {
                                console.log('FOUND WINNER ' + win_type)
                                setTimeout(() => {
                                    setSound(win_type)
                                    setShowFireworks(true)
                                }, next_call)
                                setTimeout(() => {
                                    storeTypesOfWinnersOnPlay = [
                                        winningRecords[i++] = {
                                            type: win_type,
                                            winners: getWinners[win_type],
                                            numbers: [...called_numbers, cnum]
                                        }
                                    ]
                                    setWinningTypes([...storeTypesOfWinners, ...storeTypesOfWinnersOnPlay])
                                    storeTypesOfWinners = [...storeTypesOfWinners, ...storeTypesOfWinnersOnPlay]
                                }, next_call + 3000)
                                setTimeout(() => {
                                    setWinningTypes([...storeTypesOfWinners, ...storeTypesOfWinnersOnPlay])
                                    storeTypesOfWinners = [...storeTypesOfWinners, ...storeTypesOfWinnersOnPlay]
                                }, next_call)
                                next_call += 5000
                            }
                        }
                    }
                    next_call += 7000
                    setTimeout(() => {
                        setShowFireworks(false)
                        numberCall(result, called_numbers, storeTypesOfWinners, theWinners)
                    }, next_call)
                }
            }
        }
    }
    const doColorTheCells = (row, col, number, isBoard) => {
        if (!_.isUndefined(number) && !_.isEmpty(calledNumbers) && !_.isNull(number)) {
            if (calledNumbers.includes(number)) {
                return 'bg-success'
            }
        }
        let isBoardColor = isBoard ? 'bg-primary' : 'bg-warning text-dark'
        return (parseInt(row) + parseInt(col)) % 2 == 0 ? 'bg-primary' : isBoardColor
    }

    const handleGetCustomerTickets = async () => {
        try {
            const result = await Axios.get(CUSTOMER_TICKETS)
            setCustomerTickets(result.data.ticket_customers)
        } catch (e) {
            console.log(e.message)
        }
    }

    const findCustomerName = (ticket) => {
        const customer = customerTickets[ticket]
        return undefined == customer ? '' : '(' + customer + ')'
    }

    return (
        <>
            {numberSound !== 0 && <ReactAudioPlayer
                autoPlay={true}
                loop={false}
                src={NumberAudios['./' + numberSound + '.mp3'].default}
            />}
            {sound !== 0 && <ReactAudioPlayer
                autoPlay={true}
                loop={false}
                src={GameAudios['./' + sound + '.mp3'].default}
            />}
            {showFireworks && <Fireworks {...fxProps} />}
            <Snackbar open={isSnackbarOpen} autoHideDuration={1000}>
                <Alert severity="error" elevation={6} variant="filled">
                    {isSnackbarOpen}
                </Alert>
            </Snackbar>
            <div className="container mt-5" style={{ backgroundColor: '#1A162F', opacity: '0.9' }}>
                <Carousel>
                    <Carousel.Item>
                        <div className="section-title tournament-title text-center p-3 mb-5">
                            <h2>Game <span>Started</span></h2>
                            <h2 className="mt-2">Good Luck</h2>
                        </div>
                    </Carousel.Item>
                    <Carousel.Item>
                        <div className="section-title tournament-title text-center p-3 mb-5">
                            <h2><span>Tambola</span></h2>
                            <h2 className="mt-2">Play with your <span>Near</span> &amp; <span>Dear</span></h2>
                        </div>
                    </Carousel.Item>
                    <Carousel.Item>
                        <div className="section-title tournament-title text-center p-3 mb-5">
                            <h2>Joining <span>Tournament</span></h2>
                            <h2 className="mt-2">Buy Tickets &amp; <span>Play</span></h2>
                        </div>
                    </Carousel.Item>
                </Carousel>
            </div>

            {/* <button style={{ position: 'absolute', top: '20%', right: '0' }} className="btn btn-xs d-none d-sm-none d-md-block" onClick={
                (e) => setToggleDrawer(true)
            }><FindInPageIcon /></button> */}

            {
                _.isUndefined(gameData.number_table) &&
                <LoaderComponent content={'Loading'} seconderyContent={'game...'} isBackground={true} />
            }
            {
                !_.isUndefined(gameData.number_table) &&
                <table className="container table shadow" style={{ border: '1px solid white' }}>
                    {
                        <>
                            {
                                json2array(gameData.number_table).map((numbers, index) => {
                                    return (
                                        <tr className="border-color" key={index}>
                                            {
                                                json2array(numbers).map((indexes, id) => {
                                                    return (
                                                        <>
                                                            <td className={doColorTheCells(index, id, indexes, true)} style={{ border: '1px solid white', fontWeight: 'bolder' }}>
                                                                {indexes}
                                                            </td>
                                                        </>
                                                    )
                                                })
                                            }
                                        </tr>
                                    )
                                })
                            }
                        </>
                    }
                </table>
            }

            <Row>
                <Col sm={12} className={'d-flex justify-content-center mt-2'}>
                    <button className="btn btn-xs" onClick={
                        (e) => setToggleDrawer(true)
                    }><FindInPageIcon />&emsp; Search your ticket</button>
                </Col>
            </Row>

            <SwipeableDrawer
                anchor={'right'}
                open={toggleDrawer}
                classes={{ paper: styles.paper }}
                onClose={e => setToggleDrawer(false)}
                onOpen={e => setToggleDrawer(true)}
            >
                <div className="section-title tournament-title text-center my-3">
                    <h2>Find your <span>lucky</span> number</h2>
                </div>
                <Row className={'d-flex justify-content-center ml-2'}>
                    <Col sm={5} xs={12}>
                        <input type="text" className="result" placeholder={'Ticket'} value={searchableDigits} />
                        <div className="second-row mt-2">
                            <input type="button" name="" value="7" onClick={
                                e => updateSearchableNumber('7')
                            } className="global" />
                            <input type="button" name="" value="8" onClick={
                                e => updateSearchableNumber('8')
                            } className="global" />
                            <input type="button" name="" value="9" onClick={
                                e => updateSearchableNumber('9')
                            } className="global" />
                        </div>
                        <div className="third-row">
                            <input type="button" name="" value="4" onClick={
                                e => updateSearchableNumber('4')
                            } className="global" />
                            <input type="button" name="" value="5" onClick={
                                e => updateSearchableNumber('5')
                            } className="global" />
                            <input type="button" name="" value="6" onClick={
                                e => updateSearchableNumber('6')
                            } className="global" />
                        </div>
                        <div className="fourth-row">
                            <input type="button" name="" value="1" onClick={
                                e => updateSearchableNumber('1')
                            } className="global" />
                            <input type="button" name="" value="2" onClick={
                                e => updateSearchableNumber('2')
                            } className="global" />
                            <input type="button" name="" value="3" onClick={
                                e => updateSearchableNumber('3')
                            } className="global" />
                        </div>
                        <div className="conflict">
                            <input type="button" name="" value="Clear" onClick={
                                e => {
                                    setSearchableDigits('')
                                    setSearchableTickets([])
                                }
                            } className="red white-text global" />
                            <input type="button" name="" value="0" onClick={
                                e => updateSearchableNumber('0')
                            } className="global" />
                            <input type="button" name="" onClick={
                                e => ticketsSearcher()
                            } value="Find" className="green white-text global" />
                        </div>
                    </Col>
                </Row>
                {isSearchingTicket &&
                    <LoaderComponent content={'Getting your'} seconderyContent={'tickets...'} isBackground={false} />
                }
                <table className="table mt-2 shadow">
                    {!_.isEmpty(searchableTickets) &&
                        json2array(searchableTickets).map((obj, idx) => {
                            return (
                                <>
                                    <tr style={{ backgroundColor: '#1A162F', opacity: '0.9' }}>
                                        <th className="text-center text-light" scope="col" colspan="9">LOTTO TICKET : {obj.no} {findCustomerName(obj.no)}</th>
                                    </tr>
                                    {
                                        json2array(obj.detail).map((numbers, index) => {
                                            return (
                                                <tr className="border-color" key={index}>
                                                    {
                                                        json2array(numbers).map((indexes, id) => {
                                                            return (
                                                                <>
                                                                    <td className={doColorTheCells(index, id, indexes.number, false)}>
                                                                        {indexes.number}
                                                                    </td>
                                                                </>
                                                            )
                                                        })
                                                    }
                                                </tr>
                                            )
                                        })
                                    }
                                </>
                            )
                        })
                    }
                </table>
            </SwipeableDrawer>
            <table className="container table mt-4 shadow">
                <tbody className="border-color">
                    {
                        !_.isNull(winningTypes) &&
                        !_.isUndefined(winningTypes) &&
                        // winningTypes.reverse() &&
                        winningTypes.map((obj, idx) => {
                            return (
                                <>
                                    <tr className={'mt-2'}>
                                        <div className="container pt-1" style={{ backgroundColor: '#1A162F', opacity: '0.9' }}>
                                            <h4 className="text-center p-2">{obj.type.replace(/_/g, " ").toUpperCase()}</h4>
                                        </div>
                                    </tr>
                                    <tr className="border-color">
                                        <td className="bg-success">
                                            {
                                                obj.winners.map((customer, index) => {
                                                    return (
                                                        <>
                                                            <h3><span className={'badge badge-pill badge-primary mr-1'}>{`Ticket no. ${customer.no} - (${customer.customer})`}</span></h3>
                                                        </>
                                                    )
                                                })
                                            }
                                        </td>
                                    </tr>
                                    <tr className="mb-2" style={{ backgroundColor: 'orange', opacity: '0.9', marginBottom: '1em' }} onClick={
                                        e => {
                                            setShowCard(true)
                                            setCardData({
                                                object: obj,
                                                type: obj.type.replace(/_/g, " ").toUpperCase()
                                            })
                                        }
                                    }>
                                        <td style={{ cursor: 'pointer' }}><b>View</b></td>
                                    </tr>
                                    <tr></tr>
                                </>
                            )
                        })

                    }
                </tbody>
            </table>
            {
                !_.isUndefined(calledNumbers) &&
                <div className="row">
                    <div className="col-12 text-center">
                        {
                            calledNumbers.map((obj, idx) => {
                                return (
                                    <h3 style={{ display: 'inline-block' }} className={'mr-1'}><span className="badge badge-success badge-pill">
                                        {obj}
                                    </span></h3>
                                )
                            })
                        }
                    </div>
                </div>
            }
            {
                !_.isEmpty(cardData) && <CardsDialog isShow={showCard} doColorTheCells={doColorTheCells} json2array={json2array} findCustomerName={findCustomerName} setShowCard={setShowCard} cardData={cardData} />
            }
        </>
    )
}

export default StartGame
