import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import _ from 'lodash'
import { makeStyles } from '@material-ui/core/styles'

const useStyles = makeStyles({
    paper: {
        background: 'black',
        color: 'white',
        width: '90%'
    },
    styledHeader: {
        background: 'black',
        '& h2': {
            color: 'black',
        }
    }
})

const CardsDialog = (props) => {
    const classes = useStyles()
    return (
        <>
            <Dialog
                open={props.isShow}
                onClose={e => props.setShowCard(false)}
                scroll={'paper'}
                fullWidth={true}
                classes={{ paper: classes.paper }}
                maxWidth={'md'}
                aria-labelledby="scroll-dialog-title"
                aria-describedby="scroll-dialog-description"
            >
                <DialogTitle id="scroll-dialog-title" classes={classes.styledHeader}>{props.cardData.type}</DialogTitle>
                <DialogContent dividers={true}>
                    <DialogContentText
                        id="scroll-dialog-description"
                        tabIndex={-1}
                    >
                        {!_.isEmpty(props.cardData.object)
                            &&
                            <table className="table mt-2">
                                <tr>
                                    <td>
                                        {
                                            (props.cardData.object.numbers).map((obj, idx) => {
                                                return (
                                                    <h3 style={{ display: 'inline-block' }} className={'mr-1'}><span className="badge badge-success badge-pill">
                                                        {obj}
                                                    </span></h3>
                                                )
                                            })
                                        }
                                    </td>
                                </tr>
                            </table>
                        }
                        <table className="table mt-2">
                            {!_.isEmpty(props.cardData.object)
                                &&
                                props.json2array(props.cardData.object.winners).map((obj, idx) => {
                                    return (
                                        <>
                                            <tr style={{ backgroundColor: '#1A162F', opacity: '0.9' }}>
                                                <th className="text-center text-light" colspan="9">LOTTO TICKET : {obj.no} {props.findCustomerName(obj.no)}</th>
                                            </tr>
                                            {
                                                props.json2array(obj.detail).map((numbers, index) => {
                                                    return (
                                                        <>
                                                            <tr className="border-color" key={index}>
                                                                {
                                                                    props.json2array(numbers).map((indexes, id) => {
                                                                        return (
                                                                            <>
                                                                                <td className={props.doColorTheCells(index, id, indexes.number, false)}>
                                                                                    {indexes.number}
                                                                                </td>
                                                                            </>
                                                                        )
                                                                    })
                                                                }
                                                            </tr>
                                                        </>
                                                    )
                                                })
                                            }
                                            <tr></tr>
                                        </>
                                    )
                                })
                            }
                        </table>
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={
                        e => props.setShowCard(false)
                    } color="primary">
                        Close
                    </Button>
                </DialogActions>
            </Dialog>
        </>
    )
}

export default CardsDialog