import React, { useEffect, useState } from 'react'
import { HashRouter as Router, Route, Switch } from 'react-router-dom'
import Footer from '../components/Footer'
import UpcomingGame from '../pages/UpcomingGame.jsx'
import BookingClosed from '../pages/BookingClosed.jsx'
import TambolaRules from '../pages/TambolaRules.jsx'
import StartGame from '../pages/StartGame.jsx'
import NotFound from '../pages/NotFound.jsx'
import Faq from '../pages/Faq.jsx'
import GameNotFound from '../pages/GameNotFound.jsx'
import Axios from 'axios'
import Loading from "react-fullscreen-loading"
import { GET_GAME_STATUS } from '../api/Endpoints'
import Maintenance from '../pages/Maintenance.jsx'
import _ from 'lodash'

const Routing = () => {
    const [forwardToPage, setForwardToPage] = useState()
    const fetchGameStatus = async (ticket) => {
        try {
            const status = await Axios.get(GET_GAME_STATUS)
            // setForwardToPage(status.data)
            setForwardToPage('MAINTENANCE')
        } catch (e) {
            setForwardToPage('SOMETHING_WENT_WRONG')
        }
    }

    useEffect(() => {
        const fetchMyAPI = async () => {
            await fetchGameStatus()
        }
        fetchMyAPI()
    }, [])

    return (
        <>
            <Router>
                <Switch>
                    {
                        (_.isUndefined(forwardToPage)) && <Loading loading={true} background="#00a2b7" loaderColor="#ffc035" />
                    }
                    {/* {
                        forwardToPage === 'SOMETHING_WENT_WRONG' && <Route exact path="/" component={NotFound} />
                    }
                    {
                        forwardToPage === 'VIEW_TICKETS' && <Route exact path="/" component={UpcomingGame} />
                    }
                    {
                        forwardToPage === 'BOOKING_CLOSED' && <Route exact path="/" component={BookingClosed} />
                    }
                    {
                        forwardToPage === 'PLAY' && <Route exact path="/" component={StartGame} />
                    } */}
                    {
                        forwardToPage === 'MAINTENANCE' && <Route exact path="/" component={Maintenance} />
                    }
                    <Route exact path="/pending" component={GameNotFound} />
                    <Route exact path="/rules" component={TambolaRules} />
                    <Route exact path="/faq" component={Faq} />
                    <Route path="" component={NotFound} />
                    <Route path="*" component={NotFound} />
                    <Route component={NotFound} />
                </Switch>
                <Footer />
            </Router>
        </>
    )
}

export default Routing